import { useContext } from "react"
import { Container, Image, Nav, Navbar, NavDropdown } from "react-bootstrap"
import cookie from "react-cookies"
import { Link, useNavigate } from "react-router-dom"
import { UserContext } from "../App"

function Header() {
    const [user, dispatch] = useContext(UserContext)
    const nav = useNavigate()

    const logout = (evt) => {
        evt.preventDefault()

        cookie.remove('token')
        cookie.remove('sanhid')
        cookie.remove('menuid')
        cookie.remove('dichvuid')
        
        dispatch({
            "type": "logout"
        })

        nav('/')
    }

    let btn = <>
        <Link to="/login" className="nav-link text-danger">ng nhp</Link>
        <Link to="/register" className="nav-link text-danger">ng k</Link>
    </>
    if (user != null)
        btn = <>
            <NavDropdown title={user.username} id="basic-nav-dropdown">
                <NavDropdown.Item as={Link} to={`/user/${user.id}/tieccuois`}>Lch s t tic</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item onClick={logout}>ng xut</NavDropdown.Item>
            </NavDropdown>
        </>
    
    return (
        <>
        <Navbar collapseOnSelect bg="dark" expand="lg" variant="dark" sticky="top">
            <Container>
                <Navbar.Brand as={Link} to="/">Nh hng tic ci</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="me-auto">
                    <Link className="nav-link" to="/sanh-tiec">Snh tic</Link>
                    <Link className="nav-link" to="/menu">Thc n</Link>
                    <Link className="nav-link" to="/dich-vu">Dch v</Link>
                    {btn}
                    <Link className="nav-link text-danger" to="/dat-tiec">t tic</Link>
                </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
        </>
    )
}

export default Header